"use client";
import React from "react";
import { Bottle, Bread, Trash2, Tool, Coffee, Hammer } from "lucide-react";

export const shopItems = [
  { id: 1, name: "Water Bottle", price: 10, icon: Bottle },
  { id: 2, name: "Sandwich", price: 15, icon: Bread },
  { id: 3, name: "Trash Bag", price: 5, icon: Trash2 },
  { id: 4, name: "Trash Picker Stick", price: 20, icon: Tool },
  { id: 5, name: "Coffee", price: 8, icon: Coffee },
  { id: 6, name: "Shovel", price: 25, icon: Hammer },
];

interface ShopItemCardProps {
  item: any;
  userPoints: number;
  onBuy: (id: number) => void;
}

const ShopItemCard: React.FC<ShopItemCardProps> = ({
  item,
  userPoints,
  onBuy,
}) => {
  const affordable = userPoints >= item.cost;

  return (
    <div className="flex flex-col items-center p-4 rounded-2xl shadow-lg hover:shadow-xl transition cursor-pointer w-64">
      <div className="w-32 h-32 flex items-center justify-center bg-gray-100 rounded-xl mb-3 overflow-hidden">
        <img
          src={item.imageUrl}
          alt={item.name}
          className="max-w-full max-h-full object-contain"
        />
      </div>
      <h3 className="font-bold text-lg text-green-800 mb-1">{item.name}</h3>
      <p className="text-sm text-green-700 mb-2 text-center">
        {item.description}
      </p>
      <p className="text-yellow-500 font-bold text-xl mb-2">üíé {item.cost}</p>
      <button
        disabled={!affordable}
        onClick={() => onBuy(item.id)}
        className={`w-full py-2 rounded-full font-bold text-white transition ${
          affordable
            ? "bg-green-500 hover:bg-green-600"
            : "bg-gray-400 cursor-not-allowed"
        }`}
      >
        {affordable ? "Buy üõí" : "Not enough points"}
      </button>
    </div>
  );
};

export default ShopItemCard;
